* {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  color: inherit;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

A {
  text-decoration: none;
}

/******************************index*******************************/
.page {
  height: 100vh;
  display: flex;
  flex-direction: column;
}

/*******************************main*******************************/
.card {
  flex-grow: 1;
  background: linear-gradient(to bottom, #f5f5f5, #ffffff);
}
@media all and (min-width: 768px) {
  .card {
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
    padding: 20px 40px 20px 20px;
  }
}

/*******************************photo*******************************/
.photo__container {
  box-sizing: border-box;
  background: linear-gradient(to bottom, #bfcdd0, #dde9ed);
  width: 100%;
  height: 457px;
  padding: 38px 35px 40px 45px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
@media all and (min-width: 768px) {
  .photo__container {
    border-radius: 4px;
    height: 100%;
    margin-left: 30px;
  }
}

.photo__reset--container {
  width: 240px;
  float: left;
}

.photo__reset--menu {
  width: 75px;
  height: 30px;
  border: solid 1px #ffffff;
  border-radius: 15px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  margin-bottom: 20px;
}

.photo__reset--text {
  text-decoration: none;
  text-transform: uppercase;
  font-family: "Open Sans", sans-serif;
  font-size: 12px;
  line-height: 20px;
  color: #54585a;
}

.photo__card--container {
  width: 240px;
  height: 329px;
  background-color: #ffffff;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.35);
  border-radius: 2px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
}

.photo__card--rectangle {
  background-color: #438792;
  height: 42px;
  width: 5px;
  position: absolute;
  margin: 23px 0 10px 0;
}

.photo__card--name {
  font-family: "Montserrat", sans-serif;
  font-size: 18px;
  line-height: 20px;
  font-weight: 700;
  color: #114f4f;
  width: 206px;
  padding: 21px 14px 4px 20px;
}

.photo__card--frontend {
  font-size: 16px;
  width: 168px;
  padding: 0 52px 10px 20px;
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  color: #54585a;
}

.photo__card--photo {
  height: 200px;
  width: 240px;
  background-image: url("../images/photo__imag.jpg");
  background-size: cover;
  background-position: center;
}

.photo__card--rrss {
  display: flex;
  padding: 10px 0 12px 15px;
}
.photo__card--rrss-icon {
  height: 32px;
  width: 32px;
  border: solid 2px #a2deaf;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #114f4f;
  margin: 0 5px;
}

.palette-1 .photo__card--rectangle {
  background-color: #438792;
}

.palette-1 .photo__card--name {
  color: #114f4f;
}

.palette-1 .photo__card--rrss-icon {
  border: solid 2px #a2deaf;
  color: #114f4f;
}

.palette-2 .photo__card--rectangle {
  background-color: #bd1010;
}

.palette-2 .photo__card--name {
  color: #420101;
}

.palette-2 .photo__card--rrss-icon {
  border: solid 2px #e95626;
  color: #420101;
}

.palette-3 .photo__card--rectangle {
  background-color: #eab052;
}

.palette-3 .photo__card--name {
  color: #3e5b65;
}

.palette-3 .photo__card--rrss-icon {
  border: solid 2px #a0c0cf;
  color: #3e5b65;
}

/*******************************design*******************************/
.design__legend {
  font-size: 18px;
  color: #54585a;
  font-family: "Open Sans", sans-serif;
  margin: 0 19px 0 20px;
}

.design__check {
  text-transform: uppercase;
  font-size: 14px;
  color: #54585a;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  row-gap: 20px;
  margin-left: 39px;
}

.design__check--colors {
  grid-column: 1/3;
  grid-row: 1/4;
  font-family: "Open Sans", sans-serif;
  font-size: 14px;
  color: #54585a;
}

.design__check--section {
  display: flex;
  align-items: center;
}

.design__check--section__box {
  display: flex;
  grid-column: 3/4;
  margin-left: 10px;
}
.design__check--section__box .box {
  width: 30px;
  height: 25px;
}
.design__check--section__box .box__1 {
  background-color: #114f4f;
}
.design__check--section__box .box__2 {
  background-color: #438792;
}
.design__check--section__box .box__3 {
  background-color: #a2deaf;
}
.design__check--section__box .box__4 {
  background-color: #420101;
}
.design__check--section__box .box__5 {
  background-color: #bd1010;
}
.design__check--section__box .box__6 {
  background-color: #e95626;
}
.design__check--section__box .box__7 {
  background-color: #3e5b65;
}
.design__check--section__box .box__8 {
  background-color: #eab052;
}
.design__check--section__box .box__9 {
  background-color: #a0c0cf;
}

/*******************************fill*******************************/
.form {
  margin-bottom: 10%;
}
.form__parameters {
  font-size: 18px;
  font-weight: 600;
  color: #54585a;
  text-transform: uppercase;
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.form__icon {
  color: #e17334;
  margin-right: 10px;
}
.form__title {
  display: flex;
  margin: 10px 0;
}
.form__arrow {
  color: #54585a;
  margin: 10px 0 0 auto;
  height: 18px;
  width: 16px;
}
.form__arrow--rotate {
  transform: rotate(180deg);
}
.form__line {
  border-bottom: solid 1px #d8d8d8;
}
@media all and (min-width: 768px) {
  .form {
    width: 80vw;
  }
}
@media all and (min-width: 1024px) {
  .form {
    width: 50vw;
  }
}

.fill {
  font-family: "Open Sans", sans-serif;
  margin: 0 19px 0 20px;
}
.fill__form {
  display: grid;
  grid-template-columns: 1fr 1fr;
  -moz-column-gap: 20px;
  column-gap: 20px;
}
.fill__form--label {
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  color: #114f4f;
  grid-column: span 2;
  margin-top: 4px;
}
.fill__form--input {
  border: solid #a2deaf 1px;
  height: 30px;
  margin: 7px 0 16px 0;
  grid-column: span 2;
  font-size: 15px;
  font-family: "Open Sans", sans-serif;
  padding-left: 10px;
}
.fill__form--input::-moz-placeholder {
  font-family: "Open Sans", sans-serif;
  font-size: 14px;
  line-height: 20px;
  color: #c5c5c5;
}
.fill__form--input::placeholder {
  font-family: "Open Sans", sans-serif;
  font-size: 14px;
  line-height: 20px;
  color: #c5c5c5;
}
.fill__form .photo {
  background-color: #e17334;
  color: #ffffff;
  font-family: "Open Sans", sans-serif;
  border: none;
  max-width: 140px;
  border-radius: 4px;
  height: 30px;
  grid-column: span 1;
  padding: initial;
  text-align: center;
  padding-top: 10px;
}
.fill__form .photo__square {
  width: 30px;
  height: 30px;
  grid-column: span 1;
  padding: initial;
  background-size: cover;
  background-position: center;
}

/*******************************share*******************************/
.share {
  font-family: "Open Sans", sans-serif;
  display: flex;
  flex-direction: column;
  margin: 0 19px 0 20px;
}

.dropdown {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.dropdown__button--create {
  background-color: #e17334;
  color: #ffffff;
  font-family: "Open Sans", sans-serif;
  font-size: 18px;
  text-transform: uppercase;
  font-weight: 600;
  border-radius: 12px;
  width: 199px;
  height: 50px;
  margin: 30px 24px 30px 20px;
  padding: 13px 18px 13px 17px;
  border-bottom: solid 1px #d8d8d8;
}
.dropdown__message {
  font-family: "Open Sans", sans-serif;
  font-size: 18px;
  font-weight: 600;
  color: #54585a;
}
.dropdown__button--tw {
  background-color: #00a0f5;
  color: #d8d8d8;
  font-size: 14px;
  border-radius: 3px;
  width: 181px;
  height: 30px;
  margin: 10px 33px 30px 29px;
  padding: 3px 9px 6px 10.5px;
  border-bottom: solid 1px #d8d8d8;
}

.hidden {
  display: none; }

.hide--input {
  display: none;
}

.form__header {
  position: relative;
  padding: 30px 0px 30px 0px;
}
.form__header--image {
  width: 201px;
  height: 50px;
  -o-object-fit: contain;
  object-fit: contain;
  display: block;
  margin: auto;
}
@media all and (min-width: 768px) {
  .form__header--image {
    margin-left: 40px;
  }
}

.footer {
  box-sizing: border-box;
  width: 100%;
  border-top: 1px solid #d8d8d8;
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  padding: 20px 0;
  font-family: "Open Sans", sans-serif;
}
.footer .footer__copyright {
  font-size: 14px;
  color: rgba(107, 107, 107, 0.5);
}
.footer .footer__image {
  width: 103px;
  height: 40px;
  padding-top: 20px;
}
@media all and (min-width: 768px) {
  .footer {
    flex-direction: row;
    justify-content: space-between;
    padding: 10px 40px;
  }
  .footer .footer__image {
    padding-top: 0;
  }
}

.main {
  box-sizing: border-box;
  flex-grow: 1;
  background-color: #f5f5f5;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 0 15px;
}
.main__section1 {
  width: 100%;
  padding-top: 100px;
}
.main__section2 {
  padding-bottom: 20px;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.main__section3 {
  width: 100%;
  max-width: 268px;
  font-size: 13px;
  font-family: "Open Sans", sans-serif;
  color: #54585a;
  border: 1px solid;
  border-radius: 4px;
  border-color: rgba(225, 115, 52, 0.2);
  padding: 28px 18px;
}
.main__section4 {
  padding-top: 40px;
  padding-bottom: 30px;
}

.section1__img {
  width: 100%;
  background-image: url("../images/tarjetas-molonas.png");
  background-size: contain;
  background-repeat: no-repeat;
  display: block;
  margin: auto;
  max-width: 250px;
  height: 63px;
}

.section2__title {
  padding-top: 40px;
  font-size: 20px;
  font-family: "Merriweather", serif;
  color: #e17334;
  font-weight: 900;
}

.section2__text {
  max-width: 300px;
  font-size: 13px;
  font-family: "Open Sans", sans-serif;
  color: #114f4f;
  text-align: center;
  margin: 0 20px;
  line-height: normal;
}

.list {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr;
}
.list__item {
  display: flex;
  flex-direction: column;
}
.list__img {
  color: #e17334;
  font-size: 23px;
}

.section4__button {
  background-color: #438792;
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  text-transform: uppercase;
  width: 129px;
  height: 42px;
  margin: 0 auto;
  border-radius: 20px;
}

.section4__nav {
  color: #ffffff;
}

@media all and (min-width: 768px) {
  .main__section1 {
    padding-top: 250px;
  }
  .main__section3 {
    margin: 0 auto;
    font-size: 16px;
  }
  .section1__img {
    background-image: url(../images/tarjetas-molonas@2x.png);
    max-width: 419px;
    height: 107px;
  }
  .section2__title {
    font-size: 24px;
  }
  .section2__text {
    font-size: 16px;
  }
  .section3 {
    margin-top: 10px;
  }
  .list__item {
    padding: 0 15px;
  }
  .list__img {
    color: #e17334;
    font-size: 28px;
  }
  .section4 {
    margin-top: 180px;
  }
}

